body{
  background: #ddd;
}
#root{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  color: rgb(46, 44, 44);
  border: 1px solid rgb(105, 95, 95);
  border-radius: 5px;
  background: white;
  margin: 2em 10em;
  padding: 0 4em;
  min-height: 20em;
  min-width: 32em;
  box-shadow: 0 0 1em hsla(0,0%,0%,0.15);
}

.App > .todoList> li:nth-child(odd){
  background: #eee;
}
.App > .todoList> li:nth-child(even){
  background: transparent;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*::after{
  box-sizing: border-box;
}

*::before{
  box-sizing: border-box;
}

ul,ol,dl{
  list-style: none;
}

a{
  color: inherit;
}
.TodoInput{
  width: 100%;
  line-height: 1.8;
  margin-bottom: 1em;
  font-size: 16px;
  padding: 0 .2em;
}
.TodoItem{
  padding: .5em;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}


.TodoItem .TitleWrapper{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
}

.TodoItem .TitleWrapper input{
  margin-left: .5em;
  margin-right: .5em;
}


.TodoItem>.title{
  -webkit-flex-grow: 1;
          flex-grow: 1;
  padding: 0 .5em;
}
.UserDialog-Wrapper{ 
  /*全屏*/      position: fixed; top: 0; left: 0; bottom: 0; right: 0; 
  /*内容居中*/  display: -webkit-flex;  display: flex; -webkit-justify-content: center; justify-content: center; -webkit-align-items: center; align-items: center;
  background: hsla(0,0%,0%,0.40);
}
.UserDialog{
  border: 1px solid;
  border-radius: 5px;
  min-width: 20em;
  background: white;
  border: 1px solid #aaa;
  box-shadow: 0 0 1em hsla(0,0%,0%,0.35);
}
.UserDialog .signInOrSignUp > nav:first-child{ padding: 1em; margin-left: -2em; }
.UserDialog .signInOrSignUp > nav:first-child label{ margin-left: 2em; cursor: pointer;}

.UserDialog .signInOrSignUp > .panes { padding: 0 1em 1em; }
.UserDialog .forgotPassword > h3 { padding: 1em; }
.UserDialog .forgotPassword > form { padding: 0 1em 1em; }

.UserDialog form>.row{ display: -webkit-flex; display: flex; padding: .3em 0; }
.UserDialog form>.row>label:first-child{ width: 4em; }
.UserDialog form>.row.actions{ padding-left: 4em; display: -webkit-flex; display: flex; -webkit-align-items: center; align-items: center;}
.UserDialog form>.row.actions button{ margin-right: .5em; }

.UserDialog button{font: inherit; padding: .2em;}
